body {
  font-family: "Poppins", sans-serif;
}

::selection {
  background: var(--primary);
  color: var(--white);
}

h2 {
  @apply font-extrabold text-5xl;
  color: var(--primary);
}

#hero .subtitle {
  letter-spacing: 14px;
  font-size: 18px;
}

.projects {
  width: calc(100% - 30rem);
}
.projects__item {
  width: 300px;
  height: 500px;
  position: relative;
  margin: 15px;
}
.projects__item > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.projects__item > .overlay {
  background-color: var(--overlay);
}
.projects__item .content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  padding: 20px;
  text-align: center;
  color: var(--white);
}
.projects__item .content > img {
  display: flex;
  justify-content: center;
  margin: 1rem auto;
}